@charset "UTF-8";
@import url("https://fonts.googleapis.com/css2?family=Amiko:wght@400;600;700&display=swap");
:root {
  --max-width: 1190px;
  --brand-yellow: #fcaf17;
  --brand-red: #bf1e2e;
  --brand-red-dark: #a31926;
  --brand-gray: #e9ebed;
  --font-subtitle: 22px;
  --system-black: #000000;
  --system-white: #ffffff;
  --system-gray: #cccccc;
  --system-charcoal: #5f5f5f;
  --brand-color-blue: #1a2b50;
  --brand-color-purple: #99aaff;
  --brand-color-purple2: #7484d7;
  --brand-color-purple3: #3b3b90;
  --brand-color-purple4: #1e1e6a;
  --brand-color-grey: #eeeeee;
  --brand-color-grey2: #eaebf1;
  --colour-slogan-highlight: var(--brand-color-purple);
  --colour-social-links: var(--brand-color-purple3);
  --colour-social-background: var(--brand-color-purple);
  --colour-text: var(--color-black);
  --colour-headers: var(--brand-color-blue);
  --colour-links: var(--brand-color-purple2);
  --button-hamburger: var(--brand-color-purple3);
  --button-border-purple: var(--brand-color-purple3);
  --color-menu-chevrons: var(--brand-color-purple);
  --color-submit-button: var(--brand-color-purple2);
  --color-footer-bg: var(--brand-color-purple4);
  --color-pod-bg-1: var(--brand-color-grey2);
}

* {
  outline: none;
  box-sizing: border-box;
}

html {
  overflow-y: scroll;
  box-sizing: border-box;
  font-size: 100%;
  scroll-behavior: smooth;
  background-color: white;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  color: var(--colour-text);
  font-size: 16px;
  font-optical-sizing: auto;
  margin: 0;
  padding: 0;
  font-family: "Amiko", sans-serif;
  font-weight: 400;
  font-style: normal;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: var(--colour-headers);
  font-family: "Amiko", sans-serif;
  font-weight: 600;
  font-style: normal;
  margin-top: 0;
  letter-spacing: -0.5px;
  margin-bottom: 1rem;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1 {
  font-size: 36px;
}
@media (max-width: 768px) {
  h1 {
    font-size: 28px;
  }
}

h2 {
  font-size: 28px;
}
@media (max-width: 768px) {
  h2 {
    font-size: 24px;
  }
}

h3 {
  font-size: 22px;
  font-weight: 600;
  margin-bottom: 0.75rem;
}
@media (max-width: 768px) {
  h3 {
    font-size: 20px;
  }
}

h4 {
  font-size: 18px;
}

strong {
  font-weight: 600;
}

p {
  margin-bottom: 1rem;
}

