}
.product-hero .section {
  display: flex;
  justify-content: end;
  height: 100%;
}
.product-hero .copy-wrapper {
  width: 464px;
  height: 100%;
  background-image: url("../images/bg-rectangle.png");
  background-repeat: no-repeat;
  background-position: top center;
}
.product-hero .copy-wrapper .inner-wrapper {
  margin: 0 71px 0 100px;
  height: 100%;
}
.product-hero .specs-content {
