  flex-direction: column;
  gap: 1rem;
}
.product-hero .specs-content ul {
  font-size: 0.9rem;
  height: 100%;
  overflow: scroll;
}
.product-hero .specs-content .title {
  margin: 0;
  padding: 0;
  line-height: 1;
}
.product-hero .specs-content .product-price {
