  border-bottom: 2px solid transparent;
  cursor: pointer;
  transition: all 0.33s ease-in-out;
}
.features-tabs-section .tabs-list button.active {
  border-color: var(--brand-yellow);
  cursor: default;
  font-weight: 600;
}
@media (hover: hover) {
  .features-tabs-section .tabs-list button:hover {
    font-weight: 600;
  }
}
.features-tabs-section .tab-item .header {
  line-height: 1;
  width: 100%;
  text-align: left;
  border-bottom: 1px solid var(--brand-gray);
  padding: 1rem;
  position: relative;
  margin: 0;
}
.features-tabs-section .tab-item .header:after {
  content: "\f0da";
  font-family: FontAwesome;
  font-weight: normal;
  font-style: normal;
  margin-left: 8px;
  font-size: 0.8em;
  color: var(--brand-yellow);
  position: absolute;
  right: 16px;
  top: 50%;
  transform: translateY(-50%) rotate(0deg);
  transition: all 0.3s ease-in-out;
}
.features-tabs-section .tab-item .content-wrapper {
  display: none;
}
.features-tabs-section .tab-item.active .header {
  border-bottom: 3px solid var(--brand-yellow);
}
.features-tabs-section .tab-item.active .header:after {
  transform: translateY(-50%) rotate(90deg);
}
.features-tabs-section .tab-item.active .content-wrapper {
  display: block;
}
.features-tabs-section .content-wrapper {
  padding: 1rem 0 0 0;
}
@media screen and (min-width: 769px) {
  .features-tabs-section .tab-item .header {
