.feature-list {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.feature-list li {
  display: flex;
  flex-direction: column;
  border-bottom: 1px solid var(--brand-gray);
  padding-bottom: 0.5rem;
  margin-bottom: 0.75rem;
}
