  margin-bottom: 1rem;
}
.feature-list li:last-child {
  border-bottom: none;
  margin-bottom: 0;
  padding-bottom: 0;
}
@media screen and (min-width: 768px) {
  .feature-list li {
    flex-direction: row;
    gap: 1rem;
  }
  .feature-list li .thumb {
    flex-shrink: 0;
  }
}

.downloads-list {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
  margin-bottom: 1.75rem;
}
.downloads-list li {
  position: relative;
  padding-left: 25px;
}
.downloads-list li::before {
  position: absolute;
  content: "\f1c1";
  font-family: "Font Awesome 6 Free";
  font-weight: 900;
  margin-right: 10px;
  color: var(--brand-yellow);
  left: 0;
  top: -2px;
}

.retro-fit-list {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 1rem;
}
.retro-fit-list .item .title {
  margin: 0;
  padding: 10px;
  background-color: var(--brand-gray);
  border-radius: 1px;
}
.retro-fit-list .item a {
  overflow: hidden;
}
.retro-fit-list .item a img {
  transition: all 0.3s ease-in-out;
}
@media (hover: hover) {
  .retro-fit-list .item a:hover img {
    transform: scale(1.05);
  }
}
@media screen and (min-width: 768px) {
  .retro-fit-list {
    grid-template-columns: repeat(2, 1fr);
  }
  .retro-fit-list .item {
    border-radius: 1px;
  }
}
@media screen and (min-width: 1024px) {
  .retro-fit-list {
    grid-template-columns: repeat(3, 1fr);
  }
}

.gallery-list {
  display: flex;
  gap: 0;
  overflow: scroll;
}
.gallery-list li {
  flex-shrink: 0;
  overflow: hidden;
  border-radius: 2px;
}
.gallery-list li img {
  width: 100%;
  max-width: 280px;
  height: auto;
  display: block;
  aspect-ratio: 220/148;
  object-fit: cover;
  transition: all 0.3s ease-in-out;
}
@media (hover: hover) {
  .gallery-list li:hover img {
    transform: scale(1.05);
  }
}
@media screen and (min-width: 768px) {
  .gallery-list {
    display: grid;
    gap: 0.25rem;
    grid-template-columns: repeat(2, 1fr);
  }
  .gallery-list img {
    max-width: unset !important;
  }
}
@media screen and (min-width: 920px) {
  .gallery-list {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media screen and (min-width: 1024px) {
  .gallery-list {
    grid-template-columns: repeat(4, 1fr);
  }
}
@media screen and (min-width: 1200px) {
  .gallery-list {
    grid-template-columns: repeat(5, 1fr);
  }
}

