.mobile-buttons {
  display: none;
}

.desktop-buttons {
  display: block;
}

@media (max-width: 1200px) {
  .mobile-buttons {
    display: block;
    transform: scale(1.2);
    margin-left: 10px;
    margin-top: 5px;
  }

  .desktop-buttons {
    display: none;
  }
}

.mobile-only {
  display: none !important;
}

