.body-copy {
  a {
    text-decoration: underline;

    @media (hover: hover) {
      &:hover {
        text-decoration: none;
      }
    }
  }

  @media (min-width: 1024px) {
    padding-right: 30%;
  }
}

.desktop-only {
  display: block;
}

@media (max-width: 768px) {
  .mobile-only {
    display: block !important;
  }

  .desktop-only {
    display: none !important;
  }
}

.preload {
  display: none;
}

.small-print {
  * {
    font-size: 0.95rem;
  }
}

.pre-script {
  display: none !important;
}

.imagery {
  width: 100%;
  display: block;
}

.app-loading {
  opacity: 0;
}

.tab-item-app {
  display: none;
  @media screen and (min-width: 1200px) {
    display: block;
  }
}

.section {
  padding-left: 1rem;
  padding-right: 1rem;
  margin: 0 auto;
  max-width: var(--max-width);
  margin-bottom: 48px;

  @media (max-width: 768px) {
    margin-bottom: 30px;
  }

  &.has-overlap {
    margin-bottom: 60px;
  }
}

.breadcrumbs {
  padding-left: 1rem;
  padding-right: 1rem;
  padding-bottom: 1rem;
  margin: 0 auto;
  max-width: var(--max-width);

  ol {
    border-top: 1px solid var(--brand-gray);
    display: flex;
    font-size: 0.9rem;

    li {
      display: flex;
      align-items: center;

      &::after {
        content: "/";
        margin-left: 0.5em;
        margin-right: 0.5em;
        color: var(--brand-gray);
      }

      &:last-child::after {
        content: none;
      }

      &:last-child {
        font-weight: bold;
      }
    }

    a {
      padding: 0.45rem 0;
      display: inline-block;

      @media (hover: hover) {
        &:hover {
          text-decoration: underline;
        }
      }
    }
  }
}

.title-wrapper {
  max-width: var(--max-width);
  margin: 0 auto;
  padding: 2px 1rem 2rem 1rem;
}

.video-container {
  position: relative;
  padding-bottom: 56.25%; /* 16:9 */
  padding-top: 25px;
  height: 0;
  margin-bottom: 2rem;
  overflow: hidden;

  iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: 0;
  }
}

.column-two {
  padding-bottom: 2rem;

  @media (min-width: $breakpoint3) {
    columns: 2;
    column-gap: 4rem;
  }
}

.column-three {
  padding-bottom: 2rem;

  @media (min-width: $breakpoint2) {
    columns: 2;
    column-gap: 3rem;
  }

  @media (min-width: $breakpoint3) {
    columns: 3;
    column-gap: 3rem;
  }
}

.external-link {
  padding-bottom: 1rem;

  @media (min-width: $breakpoint2) {
    padding-bottom: 2rem;
  }
}

.bttn {
  padding: 12px 16px 10px;
  font-size: 1rem;
  border-radius: 4px;
  cursor: pointer;
  line-height: 1;
  white-space: nowrap;
  text-decoration: none;
  display: inline-block;
  text-align: center;
  border: 1px solid transparent;
  border: 1px solid var(--brand-red-dark);
  transition: all 0.3s ease-in-out;
  display: inline-flex;
  gap: 12px;
  align-items: center;

  background-color: var(--brand-red-dark);
  color: white;

  i {
    color: var(--brand-yellow);
  }

  @media (hover: hover) {
    &:hover {
      background-color: var(--brand-red);
    }
  }

  &.small {
    font-size: 0.8rem;
  }

  &.bordered-bttn {
    border: 1px solid white;
  }

  &.transparent-bttn {
    background-color: transparent;
    color: white;
    text-shadow: 0 0 3px #000;
    border: 1px solid white;

    @media (hover: hover) {
      &:hover {
        background-color: white;
        color: var(--brand-red-dark);
        text-shadow: none;
      }
    }
  }
}

/* ================================================================= */
/* .back-to-top */
.back-to-top {
  display: inline-block;
  width: 44px;
  height: 44px;
  text-align: center;
  position: fixed;
  bottom: 30px;
  right: 30px;
  transition: all 0.3s ease-in-out;
  opacity: 0;
  visibility: hidden;
  z-index: 1000;

  @media (max-width: $breakpoint3) {
    display: none;
  }

  &:after {
    content: "\f077";
    font-family: FontAwesome;
    font-weight: normal;
    font-style: normal;
    font-size: 1.6em;
    line-height: 44px;
  }

  @media (hover: hover) {
    &:hover {
      cursor: pointer;
      text-decoration: none;
      color: black;
      opacity: 1;
    }
  }

  &.show {
    opacity: 1;
    visibility: visible;
  }

  @media (min-width: $breakpoint3) {
    width: 50px;
    height: 50px;

    &:after {
      font-size: 2em;
      line-height: 50px;
    }
  }
}

.dotted-list {
  list-style: none;
  padding-left: 0;
  margin-bottom: 1rem;

  li {
    position: relative;
    padding-left: 20px;
    margin-bottom: 8px;

    &::before {
      content: "\2022";
      position: absolute;
      left: 0;
      top: 0;
      color: var(--brand-yellow);
      font-size: 1.5em;
      line-height: 1;
    }
  }
}

.btn_download {
  &::after {
    content: "\f019";
    font-family: FontAwesome;
    font-weight: normal;
    font-style: normal;
    margin-left: 8px;
    font-size: 0.8em;
    color: var(--brand-yellow);
  }
}

